@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700&display=swap);
* {
    margin: 0;
    font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar {
  width: 8px;             
}

::-webkit-scrollbar-track {
  background: transparent;       
}

::-webkit-scrollbar-thumb {
  background-color: #c2c2c2;    
  border-radius: 20px;       
}



.app {
  background-color: #fff;
  overflow-x: hidden;
}


.header{display:flex;align-items:center;justify-content:space-between;width:100%;margin:0 auto;padding:40px 0;position:fixed;z-index:1000}.header .header-wrapper{display:flex;justify-content:space-between;align-items:center;margin:0 auto;max-width:1000px;width:90%}.header .header-left{height:54px;margin-left:11px}.header .header-right{margin-right:11px}.header .logo{width:160px;height:54px}.menu{width:50px;height:50px;background-color:#fff;box-shadow:rgba(50,50,50,0.1) 0px 16px 32px,rgba(0,0,0,0.1) 0px 6px 16px;position:relative;border-radius:50px;transition:ease-in-out 0.2s;z-index:100}.menu:hover{cursor:pointer;transform:scale(1.1)}.menu-icon{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.popup-menu{position:absolute;z-index:1001;width:20px;height:20px;border-radius:100%;background-image:radial-gradient(#2D56E5, #2db1e5);top:30px;right:0}.active{position:absolute;z-index:1001;transform:scale(400);border-radius:100%;top:-30px;right:0;background-image:radial-gradient()}

.hero{width:100%;height:600px;position:relative;margin:0px 0}.hero .image-container{position:relative;height:600px;width:90%;margin:180px auto;max-width:1000px}.hero .img{position:absolute;width:auto;z-index:1}.hero .img1{top:44%;right:10%;height:100px;animation:circle1 90s linear infinite}.hero .img2{top:10%;left:5%;height:300px;animation:circle2 65s linear infinite}.hero .img3{top:30%;left:45%;height:300px;animation:circle3 75s linear infinite}.hero .img4{top:50%;right:40%;height:240px;animation:circle4 55s linear infinite}.hero .img5{top:40%;right:0%;height:100px;z-index:0;opacity:0.4;animation:circle4 80s linear infinite}.hero .hero-image,.hero .content{width:90%;margin:24px auto;max-width:600px}.hero .content{display:flex}.hero .content-info{text-align:left}.hero .content-info h4{font-size:18px;font-weight:300;line-height:1.4;color:#868686}.hero .content-info .purple-text{background:-webkit-linear-gradient(#2D56E5, #C02DE5);font-weight:500;-webkit-background-clip:text;-webkit-text-fill-color:transparent}.hero .content-info .blue-text{background:-webkit-linear-gradient(#2D56E5, #2DE5DA);font-weight:500;-webkit-background-clip:text;-webkit-text-fill-color:transparent}@keyframes circle1{from{transform:rotate(0deg) translate(10px) rotate(0deg)}to{transform:rotate(360deg) translate(10px) rotate(-360deg)}}@keyframes circle2{from{transform:rotate(0deg) translate(15px) rotate(0deg)}to{transform:rotate(360deg) translate(15px) rotate(-360deg)}}@keyframes circle3{from{transform:rotate(0deg) translate(20px) rotate(0deg)}to{transform:rotate(360deg) translate(20px) rotate(-360deg)}}@keyframes circle4{from{transform:rotate(0deg) translate(20px) rotate(0deg)}to{transform:rotate(-360deg) translate(20px) rotate(360deg)}}

.menu-item{display:flex;align-items:center;flex-direction:column;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:5px 20px;width:50px;border-radius:50px;transition:ease-in-out 0.2s}.menu-item .icon{height:24px}.menu-item .title{font-size:13px;font-weight:400;text-decoration:none;color:#272727}.menu-item:hover{background-color:#eeeeee;cursor:pointer}

.sticky-menu{position:fixed;bottom:8%;width:100%;height:60px;z-index:1000}.sticky-menu .menu-wrapper{height:100%;width:100%;display:flex;align-items:center;justify-content:space-around;max-width:500px;margin:0 auto;background-color:#fff;border-radius:50px;box-shadow:rgba(50,50,50,0.1) 0px 16px 32px,rgba(0,0,0,0.1) 0px 6px 16px;transition:ease-in-out all 0.3s}.sticky-menu .menu-wrapper:hover{box-shadow:rgba(50,50,50,0.05) 0px 16px 32px,rgba(0,0,0,0.05) 0px 6px 16px}@media (max-width: 500px){.sticky-menu{bottom:0;height:70px}.sticky-menu .menu-wrapper{border-radius:0px;background-color:#ffffff}.sticky-menu .menu-wrapper:hover{cursor:inherit;padding:0}}

.portfolio-card{display:flex;flex-direction:row;max-width:720px;margin:200px auto}.portfolio-card img{height:260px;margin-right:23px;object-fit:contain}.portfolio-card .info-container{display:flex;flex-direction:column}.portfolio-card .info-container h2{font-size:20px;font-weight:400;color:#474747;padding-bottom:8px}.portfolio-card .info-container h4{font-size:13px;font-weight:300}.portfolio-card .info-container h5{padding:16px 0 30px;font-weight:500;font-size:11px;color:#000}.portfolio-card .info-container a .btn{transition:ease-in-out 0.1s}.portfolio-card .info-container a .btn h4{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:8px 30px;background-color:#2D56E5;color:#fff;text-align:center;border-radius:20px;align-items:center;transition:ease-in-out 0.1s}.portfolio-card .info-container .btn-container{display:flex;align-items:center;height:36px}.portfolio-card .info-container .code-icon{height:24px;width:24px}.portfolio-card .info-container a .code-btn{height:24px;padding:6px;border-radius:50px;background-color:#fff;box-shadow:rgba(50,50,50,0.4) 0px 0px 5px;margin-left:10px;transition:ease-in-out 0.1s}.portfolio-card .info-container a .code-btn:hover,.portfolio-card .info-container a .btn:hover{transform:translateY(-2px)}@media (max-width: 700px){.portfolio-card{flex-direction:column;align-items:center}.portfolio-card img{width:90%;margin-right:0}.portfolio-card .info-container{padding:30px 0;width:80%;max-width:500px}}

.portfolio{margin:300px 0 220px}

.bottom-fade{height:190px;width:100%;position:fixed;bottom:0;background:linear-gradient(rgba(255,255,255,0), #fff);z-index:999}

.top-fade{height:190px;width:100%;position:fixed;top:0;background:linear-gradient(#fff, rgba(255,255,255,0));z-index:999}

